body {
  font-family: "Inter", sans-serif;
  box-sizing: border-box;
}

div#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2em;
  margin: 0.5vh 1em;
}
div#root form, div#root div.CV {
  border: 1px solid gray;
  border-radius: 5px;
}

p {
  font-size: 0.9em;
}

i {
  color: #CD6666;
}

input {
  width: 100%;
}

input.name, input.description {
  width: 100%;
}

form div#form-workExperience, form div#form-education {
  margin-top: 1em;
}
form div#form-workExperience div:first-child, form div#form-education div:first-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
form div#form-workExperience div.inputSet, form div#form-education div.inputSet {
  margin-top: 5em;
}

textarea {
  width: 100%;
}

div.deleteButton {
  display: flex;
  justify-content: flex-end;
  margin-top: 0.25em;
}

div.CV {
  word-break: break-word;
}
div.CV p {
  margin-bottom: 0.5em;
}
div.CV div#cv-personalData * {
  margin: 0;
}
div.CV div#cv-personalData h1#cv-personalDataName {
  text-transform: uppercase;
  font-weight: 800;
  margin: 0;
  color: #333367;
}
div.CV div#cv-personalData #cv-personalDataPosition {
  color: #565656;
}
div.CV div#cv-workExperience h4, div.CV div#cv-education h4 {
  color: #D98462;
  font-weight: 600;
  letter-spacing: -1px;
}
div.CV div#cv-workExperience div, div.CV div#cv-education div {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  -moz-column-gap: 0.5em;
       column-gap: 0.5em;
  margin-bottom: 0.75em;
}
div.CV div#cv-workExperience div p, div.CV div#cv-education div p {
  margin: 0;
}
div.CV div#cv-workExperience div *:first-child, div.CV div#cv-education div *:first-child {
  margin-right: auto;
  font-weight: 600;
  font-size: 1em;
}
div.CV div#cv-workExperience div *:nth-child(2), div.CV div#cv-education div *:nth-child(2) {
  font-style: italic;
}
div.CV div#cv-workExperience div *:last-child, div.CV div#cv-education div *:last-child {
  flex: 0 0 100%;
}
div.CV div#cv-workExperience *:last-child, div.CV div#cv-education *:last-child {
  margin-bottom: 0;
}

@media (min-width: 500px) {
  div#root {
    margin: 1em 2em;
  }
  input {
    width: 50%;
  }
}
@media (min-width: 750px) {
  div#root {
    margin: 1.5em 3em;
  }
  input {
    width: 25%;
  }
}
@media (min-width: 1000px) {
  body {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  div#root {
    margin-top: 0;
    margin-bottom: 0;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  div#root form, div#root div.CV {
    width: 30em;
    height: 94vh;
    overflow: scroll;
  }
  input {
    width: 50%;
  }
}
@media (min-width: 1200px) {
  form, div.CV {
    width: 35em !important;
  }
}
@media (min-width: 1350px) {
  form, div.CV {
    width: 40em !important;
  }
}/*# sourceMappingURL=index.css.map */